<template>
  <div class="list_container">
    <!-- 搜索容器 -->
    <div class="search_container">
      <div class="search_item">
        <div class="search_lebel">省市区</div>
        <div class="search_content">
          <el-cascader
            clearable
            filterable
            v-model="place"
            :options="cityData"></el-cascader>
        </div>
      </div>
      <div class="search_item">
        <div class="search_lebel">姓名</div>
        <div class="search_content">
          <el-input v-model="pageQuery.visitName" placeholder="请输入姓名"></el-input>
        </div>
      </div>
      <div class="search_item">
        <div class="search_lebel">手机号</div>
        <div class="search_content">
          <el-input v-model="pageQuery.visitPhone" placeholder="请输入手机号"></el-input>
        </div>
      </div>
      <div class="search_item">
        <el-button @click="resetSearchForm" :disabled="pageLoading">重置</el-button>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="submitSearchForm" :disabled="pageLoading">搜索</el-button>
      </div>
    </div>
    <div class="search_container">
      <div class="search_item">
        <!-- 导入excel按钮 -->
        <span class="upload_excel_btn">
          <el-button type="primary"
            :loading="excelLoading"
            @click="importClick">导入excel</el-button>
          <input type="file" ref="upload-excel" style="display: none" accept=".xlsx,.xls" @change="fileChange">
        </span>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="downlaodTemplate" :disabled="pageLoading">下载excel模板</el-button>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="add">新增</el-button>
      </div>
    </div>
    <!-- 表格 -->
    <el-table :data="pageData"
      v-if="showMain"
      border>
      <el-table-column
        align="center"
        label="序号"
        prop="id">
      </el-table-column>
      <el-table-column
        align="center"
        label="接访人"
        prop="visitName">
      </el-table-column>
      <el-table-column
        align="center"
        label="接访日期"
        prop="visitDate">
        <template slot-scope="scope">
          {{scope.row.visitDate && parseTime(scope.row.visitDate, '')}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="接访来源"
        prop="visitSource">
      </el-table-column>
      <el-table-column
        align="center"
        label="地域">
        <template slot-scope="scope">
          {{scope.row.city}}{{scope.row.area}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="是否贫困户"
        prop="isPoor">
      </el-table-column>
      <el-table-column
        align="center"
        label="问题类型"
        prop="visitProblemType">
      </el-table-column>
      <el-table-column
        align="center"
        label="有效信访"
        prop="effectiveType">
      </el-table-column>
      <el-table-column
        align="center"
        label="无效信访"
        prop="invalidType">
      </el-table-column>
      <el-table-column
        align="center"
        label="信访内容"
        prop="visitContent">
      </el-table-column>
      <el-table-column
        align="center"
        label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="edit(scope.row)">编辑</el-button>
          <el-button type="text" class="del_btn" @click="del(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class="pagination_container"
      v-if="showMain">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageQuery.page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageQuery.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      width="600px"
      :close-on-click-modal="false"
      :visible.sync="dialogShow"
      :title="formStatus === 'add' ? '新增' : '编辑'">
      <el-form :model="dialogForm" ref="dialogForm" label-width="100px" class="dialog_form" :rules="rules">
        <el-row>
          <el-col :span="12">
            <el-form-item label="接访人" prop="visitName">
              <el-input v-model="dialogForm.visitName" placeholder="请输入接访人姓名"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话" prop="phone">
              <el-input v-model="dialogForm.phone" placeholder="请输入接访人电话"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="接访日期" prop="visitDate">
              <el-date-picker
                class="width100"
                v-model="dialogForm.visitDate"
                type="date"
                placeholder="选择日期">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="接访来源" prop="visitSource">
            <el-input v-model="dialogForm.visitSource" placeholder="请输入接访来源"></el-input>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="地域" prop="">
            <el-cascader
              class="width100"
              clearable
              filterable
              v-model="dialogPlace"
              :options="cityData"></el-cascader>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="是否贫困户" prop="isPoor">
              <el-select v-model="dialogForm.isPoor" placeholder="请选择">
                <el-option
                  v-for="item in poorType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="问题类型" prop="visitProblemType">
            <el-select v-model="dialogForm.visitProblemType" placeholder="请选择">
              <el-option
                v-for="item in problemType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="有效信访" prop="effectiveType">
            <el-select v-model="dialogForm.effectiveType" placeholder="请选择">
              <el-option
                v-for="item in effectiveType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="无效信访" prop="invalidType">
            <el-select v-model="dialogForm.invalidType" placeholder="请选择">
              <el-option
                v-for="item in invalidType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="信访件内容" prop="visitContent">
          <el-input v-model="dialogForm.visitContent" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import * as ChinaData from 'element-china-area-data'
import { mapActions } from 'vuex'
import DataCofig from '@/config/data'
import Utils from '@/utils'
export default {
  data () {
    return {
      showMain: true,
      dialogShow: false,
      formStatus: 'add',
      CodeToText: {},
      TextToCode: {},
      // 省市区数据
      cityData: [],
      // 上传加载中
      excelLoading: false,
      pageLoading: false,
      poorType: [],
      problemType: [],
      effectiveType: [],
      invalidType: [],
      // 搜索框省市区选择数据
      place: [],
      // 弹窗选择省市
      dialogPlace: [],
      // 提交表单
      dialogForm: {
        id: '',
        visitName: '',
        phone: '',
        visitDate: '',
        visitSource: '',
        city: '',
        area: '',
        isPoor: '',
        visitProblemType: '',
        effectiveType: '',
        invalidType: '',
        visitContent: ''
      },
      pageQuery: {
        city: '',
        area: '',
        visitPlace: '',
        visitName: '',
        visitPhone: '',
        page: 1,
        size: 10
      },
      total: 0,
      pageData: [],
      rules: {
        visitName: [
          { required: true, message: '请输入接访人姓名', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    place (val) {
      // this.pageQuery.visitPlace = val.join(',')
      const city = this.CodeToText[val[0]]
      const area = this.CodeToText[val[1]]
      this.pageQuery.city = city
      this.pageQuery.area = area
    },
    dialogPlace (val) {
      const city = this.CodeToText[val[0]]
      const area = this.CodeToText[val[1]]
      this.dialogForm.city = city
      this.dialogForm.area = area
    }
  },
  mounted () {
    this.initSettingData()
    this.initCityData()
    this.getPageData()
  },
  methods: {
    parseTime: Utils.parseTime,
    ...mapActions([
      'GET_VISIT_LIST',
      'UPLOAD_EXCEL',
      'DOWNLOAD_FILE',
      'POST_VISIT',
      'PUT_VISIT',
      'DELETE_VISIT'
    ]),
    // 新增
    add () {
      this.formStatus = 'add'
      this.dialogShow = true
    },
    // 编辑
    edit (row) {
      console.log('row:', row)
      for (let key in this.dialogForm) {
        this.dialogForm[key] = row[key]
      }
      const city = this.TextToCode[this.dialogForm.city] || {}
      const area = city[this.dialogForm.area] || {}
      this.dialogPlace = [city.code, area.code]
      this.formStatus = 'edit'
      this.dialogShow = true
    },
    // 删除
    del (row) {
      this.$confirm('确认删除这条数据吗?', '确认删除').then(() => {
        this.del2(row)
      })
    },
    async del2 (row) {
      try {
        const res = await this.DELETE_VISIT({ id: row.id })
        console.log('删除成功：', res)
        this.getPageData()
      } catch (e) {
        console.log('删除错误:', e)
      }
    },
    // 关闭弹窗
    closeDialog () {
      this.dialogPlace = []
      this.$refs.dialogForm.resetFields()
      this.formStatus = 'add'
      this.dialogShow = false
    },
    // 提交
    async submit () {
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          this.submitDlaiogForm()
        }
      })
    },
    async submitDlaiogForm () {
      console.log('dialogForm:', this.dialogForm)
      try {
        const res = this.formStatus === 'add' ? await this.POST_VISIT(this.dialogForm) : await this.PUT_VISIT(this.dialogForm)
        if (res.code === 200) {
          this.closeDialog()
          this.getPageData()
        }
        console.log('成功返回:', res)
      } catch (e) {
        console.log('失败返回:', e)
      }
    },
    importClick () {
      const dom = this.$refs['upload-excel']
      dom && dom.click()
    },
    async fileChange (e) {
      const file = e.target.files[0]
      const dom = this.$refs['upload-excel']
      dom && (dom.value = '')
      const form = new FormData()
      form.append('file', file)
      try {
        const res = await this.UPLOAD_EXCEL(form)
        console.log('成功返回:', res)
        if (res.code === 200) {
          this.pageQuery.page = 1
          this.getPageData()
          this.$message.success(res.message)
        }
      } catch (e) {
        console.log('上传excel错误:', e)
      }
    },
    // 重置搜索表单
    resetSearchForm () {
      this.place = []
      this.pageQuery.visitName = ''
      this.pageQuery.visitPhone = ''
    },
    // 提价搜索表单
    submitSearchForm () {
      this.getPageData()
    },
    // 当前页改变
    handleCurrentChange (val) {
      console.log(`当前页${val}`)
      this.getPageData()
    },
    // 每页个数改变
    handleSizeChange (val) {
      console.log(`没页个数${val}`, this.pageQuery.page)
      this.getPageData()
    },
    // 下载excel模板
    downlaodTemplate () {
      this.DOWNLOAD_FILE('public/template_excel.xlsx')
    },
    // 获取页面数据
    async getPageData () {
      try {
        const res = await this.GET_VISIT_LIST(this.pageQuery)
        if (res.code === 200 && res.data) {
          console.log('获取信访列表成功返回', res.data)
          const resData = res.data
          this.total = resData.total
          this.pageData = resData.data
        }
      } catch (e) {
        console.log('获取信访列表错误:', e)
      }
    },
    // 初始化省市区数据
    initCityData () {
      // Object.keys(ChinaData)
      // CodeToText code取对应文字
      // TextToCode 文字取对应code
      // provinceAndCityData 省市数据
      // provinceAndCityDataPlus 带全部选项的省市数据
      // regionData 省市区数据
      // regionDataPlus 带全部选项的省市区数据
      const henanCode = ChinaData.TextToCode['河南省'].code
      let cityData = []
      for (let i = 0; i < ChinaData.regionData.length; i++) {
        const province = ChinaData.regionData[i]
        if (province.value === henanCode) {
          cityData = province.children
          break
        }
      }
      // 只有河南数据
      this.cityData = cityData
      this.CodeToText = ChinaData.CodeToText
      this.TextToCode = ChinaData.TextToCode['河南省']
      // 不带全部选项
      // this.cityData = ChinaData.regionData
      // 带全部选项
      // this.cityData = ChinaData.regionData
    },
    // 初始化配置数据
    initSettingData () {
      this.poorType = DataCofig.poorType
      this.problemType = DataCofig.problemType
      this.effectiveType = DataCofig.effectiveType
      this.invalidType = DataCofig.invalidType
    }
  }
}
</script>

