  // 搜索
  .search_container {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    .search_item {
      display: flex;
      align-items: center;
      margin-right: 20px;
      margin-bottom: 20px;
      .search_lebel {
        margin-right: 10px;
      }
    }
  }
  // 分页
  .pagination_container {
    padding-top: 20px;
    display: flex;
    justify-content: flex-end;
  }
  .dialog_form {
    .el-select, .width100 {
      width: 100%;
    }
  }
  .del_btn {
    color: #f56c6c!important;
  }
}
</style>
